<template>
   <div>
           <div class="container-fluid">
                <div class="row">
                     <div class="col-3">
                          <div class="list-group">
                                   <div class="list-group-item" v-for="item in categories" :key="item" @click="searchText=item" :class="{'active':item===searchText}">
                                      <i class="fa fa-street-view"></i>{{item}}
                                   </div>
                                   <div class="list-group-item" @click="searchText=''" :class="{'active':searchText===''}"><i class="fa fa-street-view"></i>全部顯示</div>
                          </div>
                     </div>
                     <div class="col-9">
                           <div class="row d-flex align-items-center">
                                      <div class="col-4">
                                           <div class="input-group">
                                               <input type="text" class="form-control" placeholder="搜尋" v-model="searchText">
                                               <button type="button" class="input-group-append  btn btn-outline-success" @click="searchText=''">&times;</button>
                                           </div>
                                      </div>
                                      <div class="col-8">
                                               <div class="text-right">
                                                  <button type="button"  class="btn btn-outline-primary"  style="height:3rem;" @click="getProducts()">重新取得資料</button>
                                               </div>
                                      </div>
 


                           </div>



                           <div class="row mt-3">
                                    <div class="col-sm-4" v-for="item in  filterData" :key="item.id" style="height:350px;">
                                        
                                           <img :src="item.imageUrl" alt="image" style="width:100%;height:150px;">
                                            <div class="card">
                                                    <div class="card-body">
                                                            <div class="card-title text-center h4">{{item.title}}</div>
                                                            <div class="card-text">{{item.content}}</div>
                                                   </div>
                                                   <div class="card-footer">
                                                         <button type="button" class="btn btn-outline-primary" @click="addtoCart(item.id)"><i class="fas fa-box-open"></i> 加入購物車</button>

                                                   </div>
                                            </div>
                                   </div>
                             </div>
                        </div>
                 </div>
           </div>
   </div>
</template>
<script>
export default {
                  name: 'App',
                  data(){
                          return{   
                                  searchText:"",
                               }
                  },
                  methods:{
                                getProducts(){
                                          this.$store.dispatch('getProducts');
                                },
                                 addtoCart(id,qty=1){
                                        this.$store.dispatch("addtoCart",{id,qty});
                                },
                                 
                               
                    
                  },
                  computed:{
                                isLoading(){
                                                 return this.$store.state.isLoading;
                                 },
                                 products(){
                                                 return this.$store.state.products;
                                 },
                                 categories(){
                                                 return this.$store.state.categories;
                                 },
                                 filterData(){
                                                const vm=this;
                                                if(vm.searchText){
                                                                 return vm.products.filter((item)=>{
                                                                           const data=item.title.toLowerCase().includes(vm.searchText.toLowerCase());
                                                                           return data;
                                                                  })
                                                                
                                                }
                                                  return this.products;
                                 }
                  },
                  created(){
                         this.getProducts();
                 
                  }
}
</script>


