<template>
  <div id="app">
                  <nav class="navbar navbar-light bg-light">
                        <a class="navbar-brand">購物網</a>
                         <div class="dropdown ">
                               <button  type="button" class="btn btn-cart bg-dark" data-toggle="dropdown">
                                      <i class="fas fa-shopping-cart"></i>  
                                      <span class="badge badge-pill badge-danger">{{cart.carts.length}}</span>
                               </button>
                               <div class="dropdown-menu dropdown-menu-right" style="width:300px;">
                                        <h4 class="h4">已選購商品</h4>
                                        <table class="table">
                                                
                                                <tbody v-for="item in cart.carts" :key="item.id">
                                                       <tr>
                                                            <td><button type="button" class="btn"  @click="removeCart(item.id)"><i class="far fa-trash-alt"></i></button></td>
                                                            <td>{{item.product.title}}</td>
                                                            <td>{{item.qty}}/{{item.product.unit}} </td>
                                                            <td>{{cart.total}} </td>
                                                       </tr>
                                                </tbody>
                                                <tfoot>
                                                          <tr>
                                                              <td colspan="4"><button type="button" class="btn btn-outline-primary" style="width:100%;"><i class="fas fa-box-open"></i>結帳去</button></td>
                                                          </tr>

                                                </tfoot>
                                        </table>

                               </div>
                         </div>
                  </nav>
                 
                  <div class="jumbotron jumbotron-fluid">
                           <div class="container-fluid">

                                  <div class="row d-flex justify-content-center align-items-center">
                                         <div class="col-10" style="background-color:rgba(250,250,250,0.5);height:200px;">
                                             <h3 class="font-weight-bolder p-3">周年慶,特賣會</h3>
                                             <span class="lead"></span>
                                         </div>
                                  </div>

                           </div>
                  </div>
                   <Loading :active.sync="isLoading"></Loading>
                  <router-view/>
                  <footer class="mt-5">
                                 <div class="list-inline text-center">
                                      
                                          <span class="list-inline-item">&copy;2020</span>
                                          <span class="list-inline-item text-info">instagram</span>
                                          <span class="list-inline-item text-info">facebook</span>
                                          <div>用bootstrap4製作</div>
                                        
                                  </div>
                  </footer>
    
  </div>
</template>

<script>
export default {
                  name: 'App',
                  data(){
                          return{}
                  },
                  methods:{
                               getcart(){
                                          this.$store.dispatch("getCart");
                                 },
                                removeCart(id){
                                                this.$store.dispatch("removeCart",id);
                                 }
                  },
                  computed:{
                                isLoading(){
                                             return this.$store.state.isLoading;
                                 },
                                 cart(){
                                         return this.$store.state.cart;
                                 },

                                
                  },
                  created(){
                                 this.getcart();
                  }
}
</script>

<style lang="scss">
@import '~bootstrap/scss/bootstrap';
.jumbotron{background-image:url("");background-size:cover;
background-position:center; height:350px;}


</style>



